let itemArray = ['所有人可见', '仅自己可见'];
import uniNavBar from "../../../component/index/uni-nav-bar.vue";
import uploadImg from "../../../component/common/upload-img.vue"

export default {
	components: {
		uniNavBar,
		uploadImg
	},
	data() {
		return {
			titleText: "所有人可见"
		}
	},
	methods: {
		//返回键
		handleBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		//标题点击事件
		handleTitleTypeClick() {
			uni.showActionSheet({
				itemList: itemArray,
				success: (res) => {
					this.titleText = itemArray[res.tapIndex]
				}
			});
		},
		//发布按钮
		handlePublish() {
			console.log("发布按钮")
		},
		//选择图片
		handleSelectImg(imgList) {
			console.log(imgList)
		}
	}
}
